<template>
  <div>
    <div class="item">
      <!-- <img :src="lesson.thumbnails.w800" /> -->
      <div class="img" :style="imgStyle(lesson.thumbnails.w800)" />
      <h2 class="title">{{ lesson.title }}</h2>
      <div class="info">
        <span class="price">${{lesson.price}}</span>
        <span class="teacher">講師：{{lesson.teacher}}</span>
      </div>

      <p class="detail">{{ lesson.description }}</p>
      <button type="button" @click="addToCart(lesson)" :class="btnClass">{{btnLabel}}</button>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "LessonItem",
  props: ["lesson"],
  computed: {
    ...mapGetters(["isLessonInCart"]),
    inCart() {
      return this.isLessonInCart(this.lesson);
    },
    btnClass() {
      return {
        btn_addcart: true,
        bought: this.inCart
      };
    },
    btnLabel() {
      return this.inCart ? "已加入購物車" : "加入購物車";
    }
  },
  methods: {
    ...mapMutations(["addToCart"]),
    imgStyle(img) {
      return {
        backgroundImage: `url('${img}')`
      };
    }
  }
};
</script>

<style scoped lang="scss">
.item {
  position: relative;
  height: 100%;
  padding: 10px 10px 40px 10px;
  border-radius: 10px;
  -webkit-box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.3);
}
img {
  max-width: 100%;
  height: auto;
  border: 0;
}
.img {
  margin: 10px 0;
  height: 200px;
  background: center center;
  background-size: cover;
}
.title {
  color: gray;
}
.btn_addcart {
  position: absolute;
  padding: 7px 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  border: 0;
  background-color: darkseagreen;
  color: #fff;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  &:hover {
    background-color: darkcyan;
  }
}
.btn_addcart.bought {
  background-color: dimgray;
}
.info {
  color: darkorange;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;

  .teacher {
    color: lightseagreen;
  }
}
.detail {
  text-align: left;
}
</style>